import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { StreamingMedia, StreamingVideoOptions } from '@ionic-native/streaming-media';


@IonicPage()
@Component({
  selector: 'page-admin-groom-pet',
  templateUrl: 'admin-groom-pet.html',
})
export class AdminGroomPetPage {
  videos: any[] = [
    {
      title: 'Sample Video',
      video: 'https://www.youtube.com/embed/9yZEvkofi24'
    }
  ];

  constructor(public navCtrl: NavController, public navParams: NavParams, private streamingMedia: StreamingMedia) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AdminGroomPetPage');
  }

  playVideo(videoLink) {
    console.log('videoLink', videoLink);
    let options: StreamingVideoOptions = {
      successCallback: () => { console.log('Video played') },
      errorCallback: (e) => { console.log('Error streaming') },
      orientation: 'landscape'
    };
    
    this.streamingMedia.playVideo(videoLink, options);
  }
}



